import React, { useRef, useState, useEffect } from "react";
import { useGLTF, useAnimations } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import { useSpring, animated, config } from "@react-spring/three";

export function Model(props) {
  const group = useRef();
  const group2 = useRef();
  const { nodes, materials, animations } = useGLTF("bardoFace.glb");
  const { actions } = useAnimations(animations, group);

  const [rotation, setRotation] = useState(0);

  const randomRotations = [3, 10, 18, 27];

  useEffect(() => {
    const interval = setInterval(() => {
      setRotation(Math.random());
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  console.log(rotation);
  const { rotation: springRotation } = useSpring({
    rotation: [rotation, rotation, rotation],
    config: config.molasses,
  });

  useFrame(({ clock }) => {
    group2.current.rotation.y = Math.sin(clock.elapsedTime * 0.6) * 0.1;
    group2.current.rotation.x = Math.sin(clock.elapsedTime * 0.6) * 0.1;
  });
  return (
    <animated.group
      ref={group}
      {...props}
      dispose={null}
      rotation={springRotation}
    >
      <group name="Scene" ref={group2}>
        <group name="Armature">
          <primitive object={nodes.Bone} />
        </group>
      </group>
    </animated.group>
  );
}

useGLTF.preload("bardoFace.glb");
